<template>
    <div class="audio">
        <audio v-bind:src="audioLink"></audio>
        <div class="porgress-bar">
            
        </div>

        <div class="controls">
            <button @click="play">play</button>
            <button @click="stop">stop</button>
            <button @click="setSpeed(0.25)">0.25x</button>
            <button @click="setSpeed(1)">1x</button>
            <button @click="setSpeed(1.5)">1.5x</button>
            <button @click="setSpeed(2)">2x</button>
        </div>
    </div>
    
</template>

<script async="true">
export default {
    props: {
        audioLink: String
    },

    computed: {
        audio(){
            let audio = document.querySelector('audio');
            audio.loop = true;
            audio.addEventListener('ended', () => {
                console.log('ended');
            });
            return audio;
        }
    },

    methods:{
        play(){
            this.audio.play();
        },
        
        stop(){
            this.audio.pause();
        },

        setSpeed(speed){
            this.audio.pause();
            this.audio.playbackRate = speed;
            this.audio.play();
        }
    }
}
</script>

