<template>
  <div class="wrapper">
    <Start v-if="state == 'start'" @startRecord="startRecording"/>
    <Recording v-else-if="state=='recording'" @stop="pauseRecording" @cancel="cancelRecording" 
            @finish="finishRecording" v-bind:seconds="formatTime(seconds)"/>
    <Stopped v-else-if=" state=='stopped' " 
            @cancel="state = 'start'" @finish="finishRecording"
            @continue="startRecording" v-bind:seconds="formatTime(seconds)"/>
    <Uploading v-else-if="state=='uploading'" />
    <Finish v-else-if="state == 'finish'" v-bind:audioDownloadUrl="audioDownloadLink" 
      v-bind:audioListeningUrl="audioListeningLink"/>
  </div>
</template>

<script>
import Start from '@/components/Start.vue'
import Recording from '@/components/Recording.vue'
import Stopped from '@/components/Stopped.vue'
import Uploading from '@/components/Uploading.vue'
import Finish from '@/components/Finish.vue'

import $ from 'jquery'

let chunks = [];
let mediaRecorder = null;
const userMedia = navigator.mediaDevices.getUserMedia({ audio: true })
.then(stream => {
  mediaRecorder = new MediaRecorder(stream);
  mediaRecorder.ondataavailable = e => chunks.push(e.data);
});
let timer = null;


let states = {
  start: 'start',
  recording: 'recording',
  stoppped: 'stopped',
  uploading: 'uploading',
  finish: 'finish'
}

export default{
  components: { Start, Recording, Stopped, Uploading, Finish },

  data(){
    return {
      state: states.start,
      seconds: 0,
      audioDownloadLink: null,
      audioListeningLink: null
      }
  },

  methods: {
    pauseRecording(){
      this.state = states.stoppped;

      mediaRecorder.stop();

      clearInterval(timer);
    },

    startRecording(){
      this.state = states.recording;
      mediaRecorder.start();

      mediaRecorder.ondataavailable = e => {
        chunks.push(e.data);
      }

      timer = setInterval(() => {
        this.seconds++;
      }, 1000);
    },

    finishRecording(){
      this.state = states.uploading;
      if (mediaRecorder.state == 'inactive') mediaRecorder.start();
      mediaRecorder.stop();

      mediaRecorder.onstop = e => {
        let blob = new Blob(chunks, { 'type': 'audio/ogg; codecs=opus' });
        this.audioListeningLink = window.URL.createObjectURL(blob);
        chunks = [];
        // this.createAudioEl(audioURL);

        let fd = new FormData();
        fd.append('file', blob);
        console.log(fd);

        let request = $.ajax({
          url: 'http://localhost:3000/audio',
          type: 'POST',
          data: fd,
          cache: false,
          dataType: 'json',
          processData: false, 
          contentType: false,
        });

        request.done(data => {
          this.audioDownloadLink = data.audioUrl;
          this.state = states.finish;
          console.log(data);
        });
      }

      this.seconds = 0;
      clearInterval(timer);
    },

    createAudioEl(audioURL){
      let audio = document.createElement('audio');
      audio.controls = true;
      audio.src = audioURL;
      document.body.innerHTML = '';
      document.body.append(audio);
    },

    formatTime(seconds){
      let currentMinutes = Math.floor(seconds / 60);
      let currentSeconds = seconds % 60;

      let formattedMinutes = String(Math.floor(currentMinutes / 10)) + String(currentMinutes % 10);
      let formmattedSeconds = String(Math.floor(currentSeconds / 10)) + String(currentSeconds % 10);

      return formattedMinutes + ':' + formmattedSeconds;
    }
  } 
}
</script>


